import { Box, Flex, Text } from '@chakra-ui/layout'
import { useState } from 'react'
import styled from 'styled-components'
import { IoIosArrowForward, IoIosArrowDown } from 'react-icons/io'
import { useSelector, useDispatch } from 'react-redux'

const StyledItem = styled(Box)`
	margin: 0 auto 0.5em;
	background-color: transparent;
	display: flex;
	flex-direction: column;
	align-items: center;
	color: white;
	overflow-y: hidden;
	transition: all 0.3s ease;
`

export default function SideBarItem({ title, icon, children }) {
	const [isToggle, setIsToggle] = useState(false)

	return (
		<StyledItem maxH={isToggle ? '100%' : '3em'}>
			<Flex
				alignItems="center"
				cursor="pointer"
				my="1em"
				onClick={() => {
					setIsToggle((pre) => !pre)
				}}
				opacity={isToggle ? '1' : '.5'}
				_hover={{
					opacity: '1',
				}}
				pl=".5em"
			>
				<Box min-width="40px" flexShrink={0} fontSize="1.5em">
					{icon}
				</Box>

				<Text mr="1.5em" ml="1em" whiteSpace="nowrap">
					{title}
				</Text>
				{!isToggle ? <IoIosArrowForward /> : <IoIosArrowDown />}
			</Flex>
			{children ? (
				<Box borderRadius="12px" bg="white" color="#A7A7A7">
					{children.map((item, i) => (
						<Box
							ml="3em"
							mr="2.5em"
							my="1em"
							cursor="pointer"
							_hover={{ color: '#2B64D0' }}
							key={i}
						>
							{item.title}
						</Box>
					))}
				</Box>
			) : null}
		</StyledItem>
	)
}
