const StyledSideBar = styled(Box)`
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	background: #2b64d0;
	padding-top: 3rem;
	overflow: hidden;
`

const IconBox = styled(Box)`
	display: flex;
	align-items: center;
	font-size: 1.25em;
	background: white;
	border-radius: 7px;
	width: 75%;
	margin: 0 auto 3em;
	overflow: hidden;
`

export default function SideBar() {
	const SideBarMenu = useSelector((state) => state.sidebar)

	return (
		<StyledSideBar>
			<IconBox>
				<Image src={logoIcon} />
			</IconBox>
			{SideBarMenu.map((item, i) => (
				<SideBarItem
					title={item.title}
					children={item.children}
					icon={item.icon}
					isToggle={item.isToggle}
					key={i}
				/>
			))}
		</StyledSideBar>
	)
}
